@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

/* Typography*/
html {
  font-size: 100%;
}

/*16px*/
body {
  display: grid;
  place-items: center;
  background: white;
  font-family: "IBM Plex Sans", sans-serif;
  line-height: 1.75;
  color: #191826;
}

a {
  text-decoration: none;
}

/* buttons */

/* alerts */

/* forms */

/* titles */
.title {
  text-align: center;
}

/* images */
.imgContainer {
  max-width: 100%;
  border-radius: 0.25rem;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.container {
  width: 100%;
  max-width: 1440px;
  display: grid;
  place-items: center;
  padding: 0 1.5rem;
  position: relative;
  background-color: transparent;
}
.container::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100vh;
  max-width: 1440px;
  padding: 0;
  background: url("../assets/bg-main-mobile.png") no-repeat right center/cover;
  z-index: 9;
}

.header {
  width: 100%;
  display: grid;
  place-items: center;
  justify-content: start;
  gap: 64px;
}

.nav {
  display: grid;
  place-items: center;
}
.nav .imgContainer {
  width: 150px;
}

*,
::after,
::before {
  box-sizing: border-box;
}

